import React, { useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { RootState, AppDispatch } from '../app/store';
import { fetchProducts, setFilters, setPage } from '../features/products/productSlice';
import ProductCard from '../components/ProductCard';
import ProductFilter from '../components/ProductFilter';
import ProductCarousel from '../components/ProductCarousel';
import SearchBox from '../components/SearchBox';
import Message from '../components/Message';
import Loader from '../components/Loader';
import Pagination from '../components/Pagination';

const ProductsPage: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();
  const { keyword } = useParams<{ keyword: string }>();
  const location = useLocation();
  
  const { products, loading, error, filters, page, pages, totalProducts } = useSelector(
    (state: RootState) => state.products
  );

  // State cho sn phm  xut
  const [recommendedProducts, setRecommendedProducts] = useState<any[]>([]);

  // c tham s category t URL query params
  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const categoryParam = searchParams.get('category');
    
    if (categoryParam && categoryParam !== filters.category) {
      console.log('Tm thy tham s category:', categoryParam);
      dispatch(setFilters({ ...filters, category: categoryParam }));
    }
  }, [location.search, dispatch]);

  // Cp nht keyword khi c thay i t URL
  useEffect(() => {
    if (keyword) {
      dispatch(setFilters({ ...filters, keyword }));
    }
  }, [keyword, dispatch]);

  // Fetch products khi filters hoc page thay i
  useEffect(() => {
    dispatch(fetchProducts(filters));
  }, [dispatch, filters, page]);

  // To sn phm khuyn ngh t tt c sn phm
  useEffect(() => {
    if (products && products.length > 0) {
      // Lc cc sn phm khc vi danh mc hin ti hoc xo trn danh sch nu khng c lc
      let recommended = [...products];
      
      // Nu ang xem theo danh mc, u tin hin th cc sn phm cng danh mc nhng khng trng vi danh sch ang hin th
      if (filters.category) {
        recommended = recommended
          .filter(product => product.category === filters.category)
          .slice(0, 8);
      } else {
        // Xo trn mng  hin th ngu nhin
        recommended = recommended
          .sort(() => 0.5 - Math.random())
          .slice(0, 8);
      }
      
      setRecommendedProducts(recommended);
    }
  }, [products, filters.category]);

  // X l tm kim
  const handleSearch = (searchKeyword: string) => {
    console.log('ProductsPage: Tm kim vi t kha:', searchKeyword);
    dispatch(setFilters({ ...filters, keyword: searchKeyword }));
  };

  // X l chuyn trang
  const handlePageChange = (newPage: number) => {
    dispatch(setPage(newPage));
  };

  return (
    <div className="py-6">
      <h1 className="text-3xl font-bold mb-6">
        {filters.category ? `Sn phm: ${filters.category}` : 'Tt c sn phm'}
      </h1>
      
      <div className="mb-6">
        <SearchBox setKeyword={handleSearch} redirect={false} className="max-w-xl mx-auto" />
      </div>
      
      <div className="flex flex-col md:flex-row gap-6">
        {/* B lc */}
        <div className="md:w-1/4">
          <ProductFilter />
        </div>
        
        {/* Danh sch sn phm */}
        <div className="md:w-3/4">
          {loading ? (
            <Loader size="lg" text="ang ti sn phm..." />
          ) : error ? (
            <Message variant="error">{error}</Message>
          ) : products.length === 0 ? (
            <Message variant="info">
              {filters.category
                ? `Khng tm thy sn phm no trong danh mc "${filters.category}"`
                : filters.keyword
                ? `Khng tm thy sn phm no khp vi "${filters.keyword}"`
                : 'Khng tm thy sn phm no'}
            </Message>
          ) : (
            <>
              <div className="mb-4 flex justify-between items-center">
                <p className="text-gray-600">
                  Hin th {products.length} trn {totalProducts} sn phm
                </p>
                <div className="flex items-center">
                  <span className="mr-2 text-gray-600">Sp xp theo:</span>
                  <select 
                    className="border border-gray-300 rounded-md p-2"
                    onChange={(e) => {
                      // TODO: Thm sp xp sn phm
                    }}
                  >
                    <option value="newest">Mi nht</option>
                    <option value="price-asc">Gi: Thp n cao</option>
                    <option value="price-desc">Gi: Cao n thp</option>
                    <option value="rating">nh gi</option>
                  </select>
                </div>
              </div>
              
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                {products.map((product) => (
                  <ProductCard key={product._id} product={product} />
                ))}
              </div>
              
              {pages > 1 && (
                <div className="mt-8">
                  <Pagination 
                    currentPage={page} 
                    totalPages={pages} 
                    onPageChange={handlePageChange} 
                  />
                </div>
              )}
              
              {/* Sn phm  xut */}
              {recommendedProducts.length > 0 && (
                <div className="mt-16">
                  <ProductCarousel 
                    products={recommendedProducts}
                    title="Sn phm bn c th thch"
                    slidesPerView={3}
                    autoplayDelay={4000}
                    pauseOnInteraction={true}
                  />
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProductsPage; 