import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RootState, AppDispatch } from '../app/store';
import { saveShippingAddress, savePaymentMethod, clearCartItems } from '../features/cart/cartSlice';
import Message from '../components/Message';
import Loader from '../components/Loader';
import { createOrder } from '../app/api';

const CheckoutPage: React.FC = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch<AppDispatch>();
  const { cartItems, shippingAddress, paymentMethod } = useSelector((state: RootState) => state.cart);
  const { user } = useSelector((state: RootState) => state.auth);

  // Cc bc thanh ton: 1-Thng tin giao hng, 2-Phng thc thanh ton, 3-Xc nhn n hng
  const [activeStep, setActiveStep] = useState(1);
  
  // Form shipping
  const [address, setAddress] = useState(shippingAddress?.address || '');
  const [city, setCity] = useState(shippingAddress?.city || '');
  const [postalCode, setPostalCode] = useState(shippingAddress?.postalCode || '');
  const [country, setCountry] = useState(shippingAddress?.country || '');
  const [phoneNumber, setPhoneNumber] = useState(shippingAddress?.phoneNumber || '');
  
  // Phng thc thanh ton
  const [paymentMethodOption, setPaymentMethodOption] = useState(paymentMethod || 'PayPal');
  
  // Thng bo li
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  // Kim tra ng nhp v gi hng c sn phm
  useEffect(() => {
    if (!user) {
      navigate('/login?redirect=checkout');
    }
    
    if (cartItems.length === 0) {
      navigate('/cart');
    }
  }, [user, cartItems, navigate]);

  // Chuyn bc: Giao hng -> Thanh ton
  const submitShippingHandler = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!address || !city || !postalCode || !country || !phoneNumber) {
      setError('Vui lng in y  thng tin giao hng');
      return;
    }
    
    // Lu thng tin giao hng
    dispatch(saveShippingAddress({
      address,
      city,
      postalCode,
      country,
      phoneNumber,
    }));
    
    // Chuyn sang bc thanh ton
    setActiveStep(2);
    setError(null);
  };

  // Chuyn bc: Thanh ton -> Xc nhn n hng
  const submitPaymentHandler = (e: React.FormEvent) => {
    e.preventDefault();
    
    // Lu phng thc thanh ton
    dispatch(savePaymentMethod(paymentMethodOption));
    
    // Chuyn sang bc xc nhn n hng
    setActiveStep(3);
  };

  // Hon tt n hng
  const placeOrderHandler = async () => {
    setLoading(true);
    
    try {
      // To i tng n hng  gi n API
      const orderData = {
        orderItems: cartItems.map(item => ({
          name: item.name,
          qty: item.qty,
          image: item.image,
          price: item.price,
          product: item._id
        })),
        shippingAddress,
        paymentMethod: paymentMethodOption,
        totalPrice: total
      };
      
      console.log('ang gi d liu t hng:', JSON.stringify(orderData));
      console.log('Thng tin ngi dng:', JSON.stringify(user));
      
      // Gi API  to n hng
      const createdOrder = await createOrder(orderData);
      console.log('n hng  c to:', JSON.stringify(createdOrder));
      
      // Xa gi hng sau khi t hng thnh cng
      dispatch(clearCartItems());
      
      // Chuyn n trang thnh cng
      setLoading(false);
      navigate('/order-success');
    } catch (error: any) {
      console.error('Li khi t hng:', error);
      console.error('Chi tit li:', error.response?.data || error.message);
      setError(error.response?.data?.message || error.message || 't hng khng thnh cng. Vui lng th li.');
      setLoading(false);
    }
  };

  // Tnh ton tng tin
  const subtotal = cartItems.reduce((acc, item) => acc + item.price * item.qty, 0);
  const shipping = subtotal > 0 ? (subtotal < 500 ? 10 : 0) : 0;
  const tax = subtotal * 0.1; // Thu 10%
  const total = subtotal + shipping + tax;
  
  // Hin th bc thanh ton hin ti
  const renderStep = () => {
    switch (activeStep) {
      case 1:
        return (
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Thng tin giao hng</h2>
            {error && <Message variant="error">{error}</Message>}
            <form onSubmit={submitShippingHandler}>
              <div className="mb-4">
                <label htmlFor="address" className="block text-sm font-medium text-gray-700 mb-1">
                  a ch
                </label>
                <input
                  type="text"
                  id="address"
                  className="w-full p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                  placeholder="Nhp a ch ca bn"
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                />
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                <div>
                  <label htmlFor="city" className="block text-sm font-medium text-gray-700 mb-1">
                    Thnh ph
                  </label>
                  <input
                    type="text"
                    id="city"
                    className="w-full p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Nhp thnh ph"
                    value={city}
                    onChange={(e) => setCity(e.target.value)}
                  />
                </div>
                <div>
                  <label htmlFor="postalCode" className="block text-sm font-medium text-gray-700 mb-1">
                    M bu in
                  </label>
                  <input
                    type="text"
                    id="postalCode"
                    className="w-full p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Nhp m bu in"
                    value={postalCode}
                    onChange={(e) => setPostalCode(e.target.value)}
                  />
                </div>
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                <div>
                  <label htmlFor="country" className="block text-sm font-medium text-gray-700 mb-1">
                    Quc gia
                  </label>
                  <input
                    type="text"
                    id="country"
                    className="w-full p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Nhp quc gia"
                    value={country}
                    onChange={(e) => setCountry(e.target.value)}
                  />
                </div>
                <div>
                  <label htmlFor="phoneNumber" className="block text-sm font-medium text-gray-700 mb-1">
                    S in thoi
                  </label>
                  <input
                    type="tel"
                    id="phoneNumber"
                    className="w-full p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                    placeholder="Nhp s in thoi"
                    value={phoneNumber}
                    onChange={(e) => setPhoneNumber(e.target.value)}
                  />
                </div>
              </div>
              
              <div className="flex justify-end">
                <button
                  type="submit"
                  className="bg-indigo-600 text-white py-2 px-4 rounded-md hover:bg-indigo-700 transition-colors"
                >
                  Tip tc
                </button>
              </div>
            </form>
          </div>
        );
      
      case 2:
        return (
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Phng thc thanh ton</h2>
            <form onSubmit={submitPaymentHandler}>
              <div className="mb-4">
                <label className="flex items-center mb-4">
                  <input
                    type="radio"
                    name="paymentMethod"
                    value="PayPal"
                    checked={paymentMethodOption === 'PayPal'}
                    onChange={(e) => setPaymentMethodOption(e.target.value)}
                    className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300"
                  />
                  <span className="ml-2">PayPal hoc Th tn dng</span>
                </label>
                
                <label className="flex items-center mb-4">
                  <input
                    type="radio"
                    name="paymentMethod"
                    value="CreditCard"
                    checked={paymentMethodOption === 'CreditCard'}
                    onChange={(e) => setPaymentMethodOption(e.target.value)}
                    className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300"
                  />
                  <span className="ml-2">Th tn dng/ghi n</span>
                </label>
                
                <label className="flex items-center">
                  <input
                    type="radio"
                    name="paymentMethod"
                    value="COD"
                    checked={paymentMethodOption === 'COD'}
                    onChange={(e) => setPaymentMethodOption(e.target.value)}
                    className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300"
                  />
                  <span className="ml-2">Thanh ton khi nhn hng (COD)</span>
                </label>
              </div>
              
              <div className="flex justify-between">
                <button
                  type="button"
                  onClick={() => setActiveStep(1)}
                  className="bg-gray-200 text-gray-800 py-2 px-4 rounded-md hover:bg-gray-300 transition-colors"
                >
                  Quay li
                </button>
                <button
                  type="submit"
                  className="bg-indigo-600 text-white py-2 px-4 rounded-md hover:bg-indigo-700 transition-colors"
                >
                  Tip tc
                </button>
              </div>
            </form>
          </div>
        );
        
      case 3:
        return (
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">Xc nhn n hng</h2>
            {error && <Message variant="error">{error}</Message>}
            
            <div className="border-b border-gray-200 pb-4 mb-4">
              <h3 className="text-lg font-medium mb-2">Thng tin giao hng</h3>
              <p className="text-gray-600">
                <strong>a ch:</strong> {address}, {city}, {postalCode}, {country}
              </p>
              <p className="text-gray-600">
                <strong>S in thoi:</strong> {phoneNumber}
              </p>
            </div>
            
            <div className="border-b border-gray-200 pb-4 mb-4">
              <h3 className="text-lg font-medium mb-2">Phng thc thanh ton</h3>
              <p className="text-gray-600">
                <strong>Phng thc:</strong> {paymentMethodOption}
              </p>
            </div>
            
            <div className="border-b border-gray-200 pb-4 mb-4">
              <h3 className="text-lg font-medium mb-2">Sn phm t mua</h3>
              {cartItems.map(item => (
                <div key={item._id} className="flex items-center py-2 border-b border-gray-100">
                  <img src={item.image} alt={item.name} className="w-16 h-16 object-cover rounded" />
                  <div className="ml-4 flex-1">
                    <p className="font-medium">{item.name}</p>
                    <p className="text-gray-600">{item.qty} x ${item.price.toFixed(2)} = ${(item.qty * item.price).toFixed(2)}</p>
                  </div>
                </div>
              ))}
            </div>
            
            <div className="mb-6">
              <h3 className="text-lg font-medium mb-2">Tng n hng</h3>
              <div className="flex justify-between py-2">
                <span className="text-gray-600">Tm tnh:</span>
                <span>${subtotal.toFixed(2)}</span>
              </div>
              <div className="flex justify-between py-2">
                <span className="text-gray-600">Ph vn chuyn:</span>
                <span>${shipping.toFixed(2)}</span>
              </div>
              <div className="flex justify-between py-2">
                <span className="text-gray-600">Thu (10%):</span>
                <span>${tax.toFixed(2)}</span>
              </div>
              <div className="flex justify-between py-2 font-bold">
                <span>Tng cng:</span>
                <span>${total.toFixed(2)}</span>
              </div>
            </div>
            
            <div className="flex justify-between">
              <button
                type="button"
                onClick={() => setActiveStep(2)}
                className="bg-gray-200 text-gray-800 py-2 px-4 rounded-md hover:bg-gray-300 transition-colors"
              >
                Quay li
              </button>
              <button
                type="button"
                onClick={placeOrderHandler}
                disabled={loading}
                className="bg-indigo-600 text-white py-2 px-4 rounded-md hover:bg-indigo-700 transition-colors flex items-center"
              >
                {loading ? <Loader size="sm" text="ang x l..." /> : 't hng'}
              </button>
            </div>
          </div>
        );
        
      default:
        return null;
    }
  };
  
  return (
    <div className="py-8">
      <h1 className="text-3xl font-bold mb-8">Thanh ton</h1>
      
      {/* Hin th cc bc thanh ton */}
      <div className="flex justify-between mb-8">
        <div className={`flex-1 text-center ${activeStep >= 1 ? 'text-indigo-600 font-semibold' : 'text-gray-400'}`}>
          <div className={`w-8 h-8 rounded-full mx-auto flex items-center justify-center mb-2 ${activeStep >= 1 ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-400'}`}>
            1
          </div>
          <span>Thng tin giao hng</span>
        </div>
        <div className={`h-0.5 flex-1 self-center mt-0 mb-10 ${activeStep >= 2 ? 'bg-indigo-600' : 'bg-gray-200'}`}></div>
        <div className={`flex-1 text-center ${activeStep >= 2 ? 'text-indigo-600 font-semibold' : 'text-gray-400'}`}>
          <div className={`w-8 h-8 rounded-full mx-auto flex items-center justify-center mb-2 ${activeStep >= 2 ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-400'}`}>
            2
          </div>
          <span>Phng thc thanh ton</span>
        </div>
        <div className={`h-0.5 flex-1 self-center mt-0 mb-10 ${activeStep >= 3 ? 'bg-indigo-600' : 'bg-gray-200'}`}></div>
        <div className={`flex-1 text-center ${activeStep >= 3 ? 'text-indigo-600 font-semibold' : 'text-gray-400'}`}>
          <div className={`w-8 h-8 rounded-full mx-auto flex items-center justify-center mb-2 ${activeStep >= 3 ? 'bg-indigo-600 text-white' : 'bg-gray-200 text-gray-400'}`}>
            3
          </div>
          <span>Xc nhn n hng</span>
        </div>
      </div>
      
      {/* Hin th biu mu theo bc */}
      <div className="max-w-3xl mx-auto">
        {renderStep()}
      </div>
    </div>
  );
};

export default CheckoutPage; 