import axios from 'axios';
import { Product, User, Order } from '../types';
import { ProductFilters } from '../features/products/productSlice';

const API_URL = 'https://backend-3e21.onrender.com/api';

const API = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true,
});

// Thm interceptor  thm token vo header
API.interceptors.request.use((req) => {
  const userJson = localStorage.getItem('user');
  if (userJson) {
    try {
      const user = JSON.parse(userJson);
      if (user.token) {
        req.headers.Authorization = `Bearer ${user.token}`;
        console.log('Token c thm vo header:', `Bearer ${user.token.substring(0, 15)}...`);
      } else {
        console.log('Khng tm thy token trong d liu ngi dng');
      }
    } catch (error) {
      console.error('Li khi parse d liu ngi dng:', error);
    }
  } else {
    console.log('Khng tm thy d liu ngi dng trong localStorage');
  }
  return req;
});

// Product APIs
export const fetchProducts = async (filters: ProductFilters = {}) => {
  // Xy dng query params t filters
  const params = new URLSearchParams();
  
  if (filters.keyword) params.append('keyword', filters.keyword);
  if (filters.category) params.append('category', filters.category);
  if (filters.minPrice !== undefined) params.append('minPrice', filters.minPrice.toString());
  if (filters.maxPrice !== undefined) params.append('maxPrice', filters.maxPrice.toString());
  if (filters.minRating !== undefined) params.append('minRating', filters.minRating.toString());
  
  const { data } = await API.get(`/products?${params.toString()}`);
  return data;
};

export const fetchProductDetails = async (id: string) => {
  const { data } = await API.get(`/products/${id}`);
  return data;
};

export const createProductReview = async (productId: string, review: { rating: number; comment: string }) => {
  const { data } = await API.post(`/products/${productId}/reviews`, review);
  return data;
};

export const createProduct = async (productData: Partial<Product>) => {
  const response = await API.post<Product>('/products', productData);
  return response.data;
};

export const updateProduct = async (id: string, productData: Partial<Product>) => {
  const response = await API.put<Product>(`/products/${id}`, productData);
  return response.data;
};

export const deleteProduct = async (id: string) => {
  const response = await API.delete(`/products/${id}`);
  return response.data;
};

// User APIs
export const login = async (email: string, password: string) => {
  try {
    // Gi gi hng cha ng nhp (nu c)
    const anonymousCartItems = localStorage.getItem('cartItems');
    const anonymousShippingAddress = localStorage.getItem('shippingAddress');
    const anonymousPaymentMethod = localStorage.getItem('paymentMethod');
    
    const { data } = await API.post('/users/login', { email, password });
    localStorage.setItem('user', JSON.stringify(data));
    
    // Nu user  c gi hng t phin trc, u tin s dng
    const userCartItems = localStorage.getItem(`cartItems_${data._id}`);
    const userShippingAddress = localStorage.getItem(`shippingAddress_${data._id}`);
    const userPaymentMethod = localStorage.getItem(`paymentMethod_${data._id}`);
    
    console.log('ng nhp: User cart items:', userCartItems);
    console.log('ng nhp: Anonymous cart items:', anonymousCartItems);
    
    // t gi hng theo userId
    if (userCartItems) {
      console.log('S dng gi hng ca user');
      // Nu  c gi hng ca user, s dng n
      localStorage.setItem('cartItems', userCartItems);
    } else if (anonymousCartItems) {
      console.log('S dng gi hng cha ng nhp');
      // Nu khng, s dng gi hng cha ng nhp v lu cho user
      localStorage.setItem('cartItems', anonymousCartItems);
      localStorage.setItem(`cartItems_${data._id}`, anonymousCartItems);
    } else {
      console.log('Khng c gi hng no');
      // m bo cartItems l mng rng khi khng c gi hng
      localStorage.setItem('cartItems', '[]');
    }
    
    // Tng t vi a ch giao hng
    if (userShippingAddress) {
      localStorage.setItem('shippingAddress', userShippingAddress);
    } else if (anonymousShippingAddress) {
      localStorage.setItem('shippingAddress', anonymousShippingAddress);
      localStorage.setItem(`shippingAddress_${data._id}`, anonymousShippingAddress);
    } else {
      localStorage.removeItem('shippingAddress');
    }
    
    // Tng t vi phng thc thanh ton
    if (userPaymentMethod) {
      localStorage.setItem('paymentMethod', userPaymentMethod);
    } else if (anonymousPaymentMethod) {
      localStorage.setItem('paymentMethod', anonymousPaymentMethod);
      localStorage.setItem(`paymentMethod_${data._id}`, anonymousPaymentMethod);
    } else {
      localStorage.removeItem('paymentMethod');
    }
    
    return data;
  } catch (error) {
    console.error('Li ng nhp:', error);
    throw error;
  }
};

export const register = async (name: string, email: string, password: string) => {
  try {
    // Gi gi hng cha ng nhp (nu c)
    const anonymousCartItems = localStorage.getItem('cartItems');
    const anonymousShippingAddress = localStorage.getItem('shippingAddress');
    const anonymousPaymentMethod = localStorage.getItem('paymentMethod');
    
    console.log('Gi yu cu ng k:', { name, email, password: '***' });
    // Th c hai endpoint
    try {
      // Th u tin vi /users
      const { data } = await API.post('/users', { name, email, password });
      console.log('Kt qu ng k t /users:', data);
      localStorage.setItem('user', JSON.stringify(data));
      
      // Lu gi hng cho user mi
      if (anonymousCartItems) {
        localStorage.setItem(`cartItems_${data._id}`, anonymousCartItems);
        localStorage.setItem('cartItems', anonymousCartItems);
      }
      if (anonymousShippingAddress) {
        localStorage.setItem(`shippingAddress_${data._id}`, anonymousShippingAddress);
        localStorage.setItem('shippingAddress', anonymousShippingAddress);
      }
      if (anonymousPaymentMethod) {
        localStorage.setItem(`paymentMethod_${data._id}`, anonymousPaymentMethod);
        localStorage.setItem('paymentMethod', anonymousPaymentMethod);
      }
      
      return data;
    } catch (userEndpointError) {
      console.error('Li ng k t /users:', userEndpointError);
      // Nu /users khng hot ng, th vi /users/register
      const { data } = await API.post('/users/register', { name, email, password });
      console.log('Kt qu ng k t /users/register:', data);
      localStorage.setItem('user', JSON.stringify(data));
      
      // Lu gi hng cho user mi
      if (anonymousCartItems) {
        localStorage.setItem(`cartItems_${data._id}`, anonymousCartItems);
        localStorage.setItem('cartItems', anonymousCartItems);
      }
      if (anonymousShippingAddress) {
        localStorage.setItem(`shippingAddress_${data._id}`, anonymousShippingAddress);
        localStorage.setItem('shippingAddress', anonymousShippingAddress);
      }
      if (anonymousPaymentMethod) {
        localStorage.setItem(`paymentMethod_${data._id}`, anonymousPaymentMethod);
        localStorage.setItem('paymentMethod', anonymousPaymentMethod);
      }
      
      return data;
    }
  } catch (error) {
    console.error('Li ng k:', error);
    throw error;
  }
};

export const logout = async () => {
  try {
    // Lu gi hng ca user trc khi ng xut
    const cartItems = localStorage.getItem('cartItems');
    const shippingAddress = localStorage.getItem('shippingAddress');
    const paymentMethod = localStorage.getItem('paymentMethod');
    
    console.log('ng xut: Cart items trc khi ng xut:', cartItems);
    
    // Ly thng tin user
    const userJson = localStorage.getItem('user');
    if (userJson) {
      try {
        const user = JSON.parse(userJson);
        console.log('ng xut: User ID:', user._id);
        // Lu gi hng cho user ny  sau ny ng nhp li
        if (cartItems) {
          console.log('ng xut: Lu gi hng cho user', user._id);
          localStorage.setItem(`cartItems_${user._id}`, cartItems);
        }
        if (shippingAddress) localStorage.setItem(`shippingAddress_${user._id}`, shippingAddress);
        if (paymentMethod) localStorage.setItem(`paymentMethod_${user._id}`, paymentMethod);
      } catch (error) {
        console.error('Li khi lu gi hng trong logout:', error);
      }
    }
    
    // Xa gi hng hin ti v thng tin ngi dng
    localStorage.removeItem('cartItems');
    localStorage.removeItem('shippingAddress');
    localStorage.removeItem('paymentMethod');
    localStorage.removeItem('user');
    
    // t gi hng v mng rng
    localStorage.setItem('cartItems', '[]');
    
    return { success: true };
  } catch (error) {
    console.error('Li ng xut:', error);
    return { success: false, error };
  }
};

export const getUserProfile = async () => {
  const { data } = await API.get('/users/profile');
  return data;
};

export const updateUserProfile = async (userData: any) => {
  const { data } = await API.put('/users/profile', userData);
  
  // Cp nht thng tin user trong localStorage
  const userJson = localStorage.getItem('user');
  if (userJson) {
    const user = JSON.parse(userJson);
    localStorage.setItem('user', JSON.stringify({ ...user, ...data }));
  }
  
  return data;
};

