import React, { useEffect, useState } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { RootState } from '../../app/store';
import { getOrderDetails, payOrder, updateOrderToDelivered, cancelOrder } from '../../app/api';
import Loader from '../../components/Loader';
import Message from '../../components/Message';
import { Order } from '../../types';
import axios from 'axios';

const AdminOrderDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { user } = useSelector((state: RootState) => state.auth);
  
  const [order, setOrder] = useState<Order | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [loadingDeliver, setLoadingDeliver] = useState(false);
  const [deliverSuccess, setDeliverSuccess] = useState(false);
  const [deliverError, setDeliverError] = useState<string | null>(null);
  const [loadingCancel, setLoadingCancel] = useState(false);
  const [cancelSuccess, setCancelSuccess] = useState(false);
  const [cancelError, setCancelError] = useState<string | null>(null);

  useEffect(() => {
    // Kim tra ng nhp v quyn admin
    if (!user || !user.isAdmin) {
      navigate('/login');
      return;
    }

    const fetchOrderDetails = async () => {
      try {
        setLoading(true);
        console.log('AdminOrderDetail: ang ly thng tin n hng ID:', id);
        
        if (id) {
          const data = await getOrderDetails(id);
          console.log('AdminOrderDetail: Kt qu:', data);
          setOrder(data);
        }
        
        setLoading(false);
      } catch (err: any) {
        console.error('AdminOrderDetail: Li khi ly n hng:', err);
        console.error('AdminOrderDetail: Chi tit li:', err.response?.data || err.message);
        setError(err.response?.data?.message || err.message || 'Khng th ti thng tin n hng');
        setLoading(false);
      }
    };

    fetchOrderDetails();
  }, [id, user, navigate, deliverSuccess, cancelSuccess]);

  // X l nh du  giao hng
  const markAsDeliveredHandler = async () => {
    try {
      setLoadingDeliver(true);
      setDeliverError(null);
      
      if (!id) return;
      
      console.log('AdminOrderDetail: nh du  giao hng cho n hng ID:', id);
      const updatedOrder = await updateOrderToDelivered(id);
      setOrder(updatedOrder);
      setDeliverSuccess(true);
      setLoadingDeliver(false);
    } catch (err: any) {
      console.error('AdminOrderDetail: Li khi cp nht trng thi giao hng:', err);
      console.error('AdminOrderDetail: Chi tit li:', err.response?.data || err.message);
      setDeliverError(err.response?.data?.message || err.message || 'Khng th cp nht trng thi giao hng');
      setDeliverSuccess(false);
      setLoadingDeliver(false);
    }
  };

  const handleCancelOrder = async () => {
    // Hin th xc nhn
    if (!window.confirm('Bn c chc chn mun hy n hng ny khng?')) {
      return;
    }
    
    try {
      setLoadingCancel(true);
      setCancelError(null);
      
      if (id) {
        const updatedOrder = await cancelOrder(id);
        setOrder(updatedOrder);
        setCancelSuccess(true);
      }
      
      setLoadingCancel(false);
    } catch (err: any) {
      setCancelError(err.response?.data?.message || 'Khng th hy n hng');
      setLoadingCancel(false);
    }
  };

  // Format ngy thng
  const formatDate = (dateString?: string) => {
    if (!dateString) return 'N/A';
    
    const options: Intl.DateTimeFormatOptions = { 
      year: 'numeric', 
      month: '2-digit', 
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    };
    return new Date(dateString).toLocaleDateString('vi-VN', options);
  };

  if (loading) {
    return <Loader size="lg" text="ang ti thng tin n hng..." />;
  }

  if (error) {
    return <Message variant="error">{error}</Message>;
  }

  if (!order) {
    return <Message variant="error">Khng tm thy n hng</Message>;
  }

  return (
    <div className="py-8">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-2xl font-bold">Chi tit n hng (Admin)</h1>
        <div>
          <Link
            to="/admin/orders"
            className="text-indigo-600 hover:text-indigo-800 mr-4"
          >
            Quay li danh sch n hng
          </Link>
        </div>
      </div>

      {deliverSuccess && (
        <Message variant="success" className="mb-4">
          n hng  c cp nht thnh cng
        </Message>
      )}

      {deliverError && (
        <Message variant="error" className="mb-4">
          {deliverError}
        </Message>
      )}

      {cancelSuccess && (
        <Message variant="success" className="mb-4">
          n hng  c hy thnh cng
        </Message>
      )}

      {cancelError && (
        <Message variant="error" className="mb-4">
          {cancelError}
        </Message>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Thng tin n hng */}
        <div className="lg:col-span-2 space-y-6">
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4 pb-2 border-b border-gray-200">
              M n hng: {order._id}
            </h2>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <h3 className="font-medium text-gray-800 mb-2">Thng tin n hng</h3>
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Khch hng:</span> {order.user.name}
                </p>
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Email:</span> {order.user.email}
                </p>
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Ngy t:</span> {formatDate(order.createdAt)}
                </p>
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Trng thi thanh ton:</span>{' '}
                  {order.isPaid ? (
                    <span className="text-green-600"> thanh ton ({formatDate(order.paidAt)})</span>
                  ) : (
                    <span className="text-red-600">Cha thanh ton</span>
                  )}
                </p>
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Trng thi giao hng:</span>{' '}
                  {order.isDelivered ? (
                    <span className="text-green-600"> giao hng ({formatDate(order.deliveredAt)})</span>
                  ) : (
                    <span className="text-yellow-600">ang x l</span>
                  )}
                </p>
                {order.isCancelled && (
                  <p className="text-sm text-gray-600 mb-1">
                    <span className="font-medium">Trng thi n hng:</span>{' '}
                    <span className="text-red-600"> hy ({formatDate(order.cancelledAt)})</span>
                  </p>
                )}
                <p className="text-sm text-gray-600 mb-1">
                  <span className="font-medium">Phng thc thanh ton:</span> {order.paymentMethod}
                </p>
              </div>
              
              <div>
                <h3 className="font-medium text-gray-800 mb-2">a ch giao hng</h3>
                <p className="text-sm text-gray-600">
                  {order.shippingAddress.address}, {order.shippingAddress.city},{' '}
                  {order.shippingAddress.postalCode}, {order.shippingAddress.country}
                </p>
                {order.shippingAddress.phoneNumber && (
                  <p className="text-sm text-gray-600 mt-1">
                    <span className="font-medium">S in thoi:</span> {order.shippingAddress.phoneNumber}
                  </p>
                )}
              </div>
            </div>
            
            {/* Admin Actions */}
            <div className="mt-6 pt-4 border-t border-gray-200">
              <h3 className="font-medium text-gray-800 mb-3">Thao tc qun tr</h3>
              <div className="flex flex-wrap gap-3">
                {order.isPaid && !order.isDelivered && !order.isCancelled && (
                  <button
                    onClick={markAsDeliveredHandler}
                    disabled={loadingDeliver}
                    className="bg-green-600 text-white py-2 px-4 rounded-md hover:bg-green-700 transition-colors disabled:opacity-50"
                  >
                    {loadingDeliver ? <Loader size="sm" text="ang x l..." /> : 'nh du  giao hng'}
                  </button>
                )}
                
                {!order.isPaid && (
                  <button
                    className="bg-yellow-500 text-white py-2 px-4 rounded-md hover:bg-yellow-600 transition-colors"
                    disabled
                  >
                    Ch thanh ton
                  </button>
                )}

                {!order.isDelivered && !order.isCancelled && order.isPaid && (
                  <button
                    onClick={handleCancelOrder}
                    disabled={loadingCancel}
                    className="bg-red-600 text-white py-2 px-4 rounded-md hover:bg-red-700 transition-colors disabled:opacity-50 ml-3"
                  >
                    {loadingCancel ? <Loader size="sm" text="ang x l..." /> : 'Hy n hng'}
                  </button>
                )}
              </div>
            </div>
          </div>

          {/* Danh sch sn phm */}
          <div className="bg-white rounded-lg shadow-md overflow-hidden">
            <h2 className="text-xl font-semibold p-6 pb-4 border-b border-gray-200">
              Sn phm t mua
            </h2>
            <div className="divide-y divide-gray-200">
              {order.orderItems.map((item) => (
                <div key={item.product} className="p-6 flex items-center">
                  <div className="h-20 w-20 flex-shrink-0">
                    <img
                      src={item.image}
                      alt={item.name}
                      className="h-20 w-20 object-cover rounded"
                    />
                  </div>
                  <div className="ml-4 flex-1">
                    <Link
                      to={`/product/${item.product}`}
                      className="text-lg font-medium text-indigo-600 hover:text-indigo-800"
                    >
                      {item.name}
                    </Link>
                    <p className="text-gray-600 mt-1">
                      {item.qty} x ${item.price.toFixed(2)} = ${(item.qty * item.price).toFixed(2)}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Tng n hng */}
        <div className="lg:col-span-1">
          <div className="bg-white rounded-lg shadow-md p-6 sticky top-4">
            <h2 className="text-xl font-semibold mb-4 pb-2 border-b border-gray-200">
              Tng n hng
            </h2>
            
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-gray-600">Tm tnh:</span>
                <span>
                  ${order.orderItems.reduce((acc, item) => acc + item.qty * item.price, 0).toFixed(2)}
                </span>
              </div>
              
              <div className="flex justify-between">
                <span className="text-gray-600">Ph vn chuyn:</span>
                <span>$10.00</span>
              </div>
              
              <div className="flex justify-between">
                <span className="text-gray-600">Thu (10%):</span>
                <span>
                  ${(order.totalPrice * 0.1).toFixed(2)}
                </span>
              </div>
              
              <div className="flex justify-between pt-3 border-t border-gray-200 font-bold">
                <span>Tng cng:</span>
                <span className="text-xl text-indigo-600">${order.totalPrice.toFixed(2)}</span>
              </div>
              
              {/* Trng thi n hng */}
              <div className="pt-4 mt-2">
                <div className="p-3 rounded-md bg-gray-50">
                  <h3 className="text-lg font-medium mb-2">Trng thi n hng</h3>
                  <ul className="space-y-2">
                    <li className="flex items-center">
                      <div className={`w-3 h-3 rounded-full mr-2 ${order.isPaid ? 'bg-green-500' : 'bg-red-500'}`}></div>
                      <span>{order.isPaid ? ' thanh ton' : 'Cha thanh ton'}</span>
                    </li>
                    <li className="flex items-center">
                      <div className={`w-3 h-3 rounded-full mr-2 ${order.isDelivered ? 'bg-green-500' : 'bg-yellow-500'}`}></div>
                      <span>{order.isDelivered ? ' giao hng' : 'ang x l'}</span>
                    </li>
                    <li className="flex items-center">
                      <div className={`w-3 h-3 rounded-full mr-2 ${order.isCancelled ? 'bg-red-500' : 'bg-gray-500'}`}></div>
                      <span>{order.isCancelled ? ' hy' : 'Cha hy'}</span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminOrderDetail; 