import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { CartItem, ShippingAddress } from '../../types';

interface CartState {
  cartItems: CartItem[];
  shippingAddress: ShippingAddress | null;
  paymentMethod: string;
  loading: boolean;
}

// Get cart items from localStorage
const cartItemsFromStorage: CartItem[] = localStorage.getItem('cartItems')
  ? JSON.parse(localStorage.getItem('cartItems') || '[]')
  : [];

// Get shipping address from localStorage
const shippingAddressFromStorage: ShippingAddress | null = localStorage.getItem(
  'shippingAddress'
)
  ? JSON.parse(localStorage.getItem('shippingAddress') || 'null')
  : null;

// Get payment method from localStorage
const paymentMethodFromStorage: string = localStorage.getItem('paymentMethod')
  ? JSON.parse(localStorage.getItem('paymentMethod') || '""')
  : '';

const initialState: CartState = {
  cartItems: cartItemsFromStorage,
  shippingAddress: shippingAddressFromStorage,
  paymentMethod: paymentMethodFromStorage,
  loading: false,
};

// Helper  lu gi hng theo userId
const saveCartForUser = (cartItems: CartItem[]) => {
  const userJson = localStorage.getItem('user');
  if (userJson) {
    try {
      const user = JSON.parse(userJson);
      if (user._id) {
        console.log('Lu gi hng cho user:', user._id, 'S lng sn phm:', cartItems.length);
        localStorage.setItem(`cartItems_${user._id}`, JSON.stringify(cartItems));
        return true;
      } else {
        console.warn('User khng c ID');
      }
    } catch (error) {
      console.error('Li khi lu gi hng theo user:', error);
    }
  } else {
    console.log('Khng c user ng nhp, khng lu gi hng theo user');
  }
  return false;
};

const cartSlice = createSlice({
  name: 'cart',
  initialState,
  reducers: {
    addToCart: (state, action: PayloadAction<CartItem>) => {
      state.loading = true;
      const item = action.payload;
      const existItem = state.cartItems.find((x) => x._id === item._id);

      if (existItem) {
        state.cartItems = state.cartItems.map((x) =>
          x._id === existItem._id ? item : x
        );
      } else {
        state.cartItems = [...state.cartItems, item];
      }

      // Save to localStorage
      localStorage.setItem('cartItems', JSON.stringify(state.cartItems));
      // Lu theo userId nu  ng nhp
      saveCartForUser(state.cartItems);
      
      state.loading = false;
    },
    removeFromCart: (state, action: PayloadAction<string>) => {
      state.loading = true;
      state.cartItems = state.cartItems.filter((x) => x._id !== action.payload);
      
      // Save to localStorage
      localStorage.setItem('cartItems', JSON.stringify(state.cartItems));
      // Lu theo userId nu  ng nhp
      saveCartForUser(state.cartItems);
      
      state.loading = false;
    },
    saveShippingAddress: (state, action: PayloadAction<ShippingAddress>) => {
      state.shippingAddress = action.payload;
      
      // Save to localStorage
      localStorage.setItem('shippingAddress', JSON.stringify(action.payload));
      
      // Lu a ch theo user (nu  ng nhp)
      const userJson = localStorage.getItem('user');
      if (userJson) {
        try {
          const user = JSON.parse(userJson);
          if (user._id) {
            localStorage.setItem(`shippingAddress_${user._id}`, JSON.stringify(action.payload));
          }
        } catch (error) {
          console.error('Li khi lu a ch giao hng theo user:', error);
        }
      }
    },
    savePaymentMethod: (state, action: PayloadAction<string>) => {
      state.paymentMethod = action.payload;
      
      // Save to localStorage
      localStorage.setItem('paymentMethod', JSON.stringify(action.payload));
      
      // Lu phng thc thanh ton theo user (nu  ng nhp)
      const userJson = localStorage.getItem('user');
      if (userJson) {
        try {
          const user = JSON.parse(userJson);
          if (user._id) {
            localStorage.setItem(`paymentMethod_${user._id}`, JSON.stringify(action.payload));
          }
        } catch (error) {
          console.error('Li khi lu phng thc thanh ton theo user:', error);
        }
      }
    },
    clearCartItems: (state) => {
      state.cartItems = [];
      localStorage.removeItem('cartItems');
      
      // Xa gi hng theo user (nu  ng nhp)
      const userJson = localStorage.getItem('user');
      if (userJson) {
        try {
          const user = JSON.parse(userJson);
          if (user._id) {
            localStorage.removeItem(`cartItems_${user._id}`);
          }
        } catch (error) {
          console.error('Li khi xa gi hng theo user:', error);
        }
      }
    },
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.loading = action.payload;
    },
    // ng b gi hng t localStorage (sau khi ng nhp/ng xut)
    syncCartFromStorage: (state) => {
      try {
        console.log('ng b gi hng t localStorage');
        const cartItemsJson = localStorage.getItem('cartItems');
        const shippingAddressJson = localStorage.getItem('shippingAddress');
        const paymentMethodJson = localStorage.getItem('paymentMethod');
        
        console.log('D liu gi hng t localStorage:', cartItemsJson);
        
        const cartItemsFromStorage = cartItemsJson 
          ? JSON.parse(cartItemsJson) 
          : [];
          
        const shippingAddressFromStorage = shippingAddressJson
          ? JSON.parse(shippingAddressJson)
          : null;
          
        const paymentMethodFromStorage = paymentMethodJson
          ? JSON.parse(paymentMethodJson)
          : '';
          
        console.log('S lng sn phm trong gi hng:', cartItemsFromStorage.length);
          
        state.cartItems = cartItemsFromStorage;
        state.shippingAddress = shippingAddressFromStorage;
        state.paymentMethod = paymentMethodFromStorage;
      } catch (error) {
        console.error('Li khi ng b gi hng:', error);
        // Nu c li, t v mng rng  trnh crash
        state.cartItems = [];
      }
    }
  },
});

export const {
  addToCart,
  removeFromCart,
  saveShippingAddress,
  savePaymentMethod,
  clearCartItems,
  setLoading,
  syncCartFromStorage
} = cartSlice.actions;

export default cartSlice.reducer; 